class ListItem extends React.Component {
  copyKart = () => {
    console.log('this ma wartość:', this.props.CardLink);
    window.navigator.clipboard.writeText(this.props.CardLink);
  }
  copyOpi = () => {
    console.log('this ma wartość:', this.props.description);
    window.navigator.clipboard.writeText(this.props.description);
  }
  
  render() {
    const {image,name,description,CardLink,idProduktu} = this.props;

    const ImageTag = image ? 'img' : 'div';

  return (
    <div className={styles.item__wrapper} id={idProduktu}>

      <ImageTag
        src={image}
        className={image ? styles.image : styles.imageNone}
        alt={name} />


      <div className={styles.item__name}>
        <div className={styles.divNear__img}>


          {name}
        </div>
      </div>
      <div className={styles.item__card} onClick={(e) => this.copyKart(e)}>
        <span className={styles.copy_card}>
          {CardLink}
        </span>
        <div title="KARTA KATALOGOWA" >KARTA/KAT</div>
      </div>
      <div className={styles.item__describe} onClick={(e) => this.copyOpi(e)}>
        <span className={styles.copy_describe}>
          {description}
        </span>
        <div title="OPIS PRODUKTU">OPIS/ITEM</div>
      </div>

    </div>
    )
  }

  
  
};

ListItem.propTypes = {
  image: PropTypes.string,
  name: PropTypes.string.isRequired,
  description: PropTypes.string,
  CardLink: PropTypes.string.isRequired,
  idProduktu: PropTypes.string,
};

ListItem.defaultProps = {
  description: "Karta katalogowa",
  image: null,
  idProduktu: null,
};



